import React, { useState,Fragment } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { deleteItemsFromCartAsync, selectCartItems, selectCartLoaded, updateCartAsync } from './cartSlice';
import { Link, Navigate } from 'react-router-dom';
import { selectUserChecked } from '../auth/authSlice';



export default function Cart() {
  const items = useSelector(selectCartItems)
  const dispatch = useDispatch();
  const [open, setOpen] = useState(true)
   const totalAmount = items.reduce((amount,item)=>item.Product.price*item.quantity+amount,0)
  const UserChecked = useSelector(selectUserChecked)
  const totalItems = items.reduce((total,item)=>
    item.quantity+total,0)
  const cartLoaded = useSelector(selectCartLoaded)
  const handleQuantity = (e,item)=>{
    dispatch(updateCartAsync({id:item.id,quantity:+e.target.value}))
  }

  const handleRemove= (e,id)=>{
    dispatch(deleteItemsFromCartAsync(id))
  }
  return (
    <>
     {!items.length>0 && <Navigate to='/empty-cart' replace={true}></Navigate>}
   
     <div className="mx-auto max-w-7xl px-4 mt-7 sm:px-6 lg:px-8">
     <h2 className="text-3xl mb-3">Cart</h2>
                        <div className="flow-root mb-4">
                          <ul role="list" className="-my-6 divide-y divide-gray-200">
                            {items.map((item) => (
                              <li key={item.id} className="flex py-6">
                                <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                                  <img
                                    src={item.Product.images[0]}
                                    alt={item.Product.images[0]}
                                    className="h-full w-full object-cover object-center"
                                  />
                                </div>

                                <div className="ml-4 flex flex-1 flex-col">
                                  <div>
                                    <div className="flex justify-between text-base font-medium text-gray-900">
                                      <h3>
                                        <p>{item.Product.title}</p>
                                      </h3>
                                      <p className="ml-4">${item.Product.price}</p>
                                    </div>
                                    <p className="mt-1 text-sm text-gray-500">{item.Product.color}</p>
                                  </div>
                                  <div className="flex flex-1 items-end justify-between text-sm">
                                    <div
                                    className="text-gray-500">
                                      <label htmlFor="password" className="inline text-sm font-medium leading-6 text-gray-900">
                                    </label>Qty
              
                                    <select 
                                    onChange={e=>handleQuantity(e,item)} value={item.quantity}
                                    className='ml-3'>
                                      <option value='1'>1</option>
                                      <option value='2'>2</option>
                                      <option value='3'>3</option>
                                      <option value='4'>4</option>
                                    </select>
                                    </div>

                                    <div className="flex">
                                      <button
                                      onClick={e=>handleRemove(e,item.id)}
                                        type="button"
                                        className="font-medium text-indigo-600 hover:text-indigo-500"
                                      >
                                        Remove
                                      </button>
                                    </div>
                                  </div>
                                </div>
                              </li>
                            ))}
                          </ul>
                        </div>

                        <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
                      <div className="flex justify-between text-base font-medium text-gray-900">
                        <p>Subtotal</p>
                        <p>${totalAmount}</p>
                      </div>
                      <div className="flex justify-between text-base font-medium text-gray-900">
                        <p>Total items in cart</p>
                        <p>{totalItems}</p>
                      </div>
                      <p className="mt-0.5 text-sm text-gray-500">Shipping and taxes calculated at checkout.</p>
                      <div className="mt-6">
                        <Link
                          to='/checkout'
                          className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700"
                        >
                        Checkout
                        </Link>
                      </div>
                      <div className="mt-6 flex justify-center text-center text-sm text-gray-500">
                        <p>
                          or
                          <Link to='/'>
                          <button
                            type="button"
                            className="font-medium text-indigo-600 hover:text-indigo-500"
                            onClick={() => setOpen(false)}
                          >
                            Continue Shopping
                            <span aria-hidden="true"> &rarr;</span>
                          </button>
                          </Link>
                        </p>
                      </div>
                    </div>
              </div>
    </>          
                    
  );
}
